<script>
import backBtn from '../components/mats/backBtn.vue'
import headerProfile from '../components/profile/headerProfile.vue'
import album from '../components/profile/album.vue'
import storyCard from '../components/mid/storyCard.vue'

  export default{
    name: 'Profile',
    components: {
      backBtn,
      headerProfile,
      album,
      storyCard
    },
    data() {
      return {
        name: (this.$route.params.username) ? this.$route.params.username : '',
        sampul: '../assets/post/kota.jpg',
        fullName: 'Tri Yadi Mulyana',
        desc: 'Consectetur a deleniti commodi inventore quidem Modi voluptate alias voluptas blanditiis numquam.',
