        },
        token: localStorage.getItem('xtoken')
      }
    },
    props: {
      warna: Object
    },
    methods: {
      async getData(){
        alert(this.token)
        try {
          const data = await this.axios.get('/Postdata/me', {
            headers: {
              Authorization: `Bearer ${this.token}`
            }
          });
          alert(data);
        } catch (e) {
          alert(e)
        }
      }
    },
    created() {
      this.$emit('hilang-navbar', false)
      this.getData();
    }

  }
</script>

